<template>
  <div class="">
    <!-- Modal Component -->
    <b-modal ref="modalAddChannel" 
             id="modal-add-channel"
             class="modal-platform"
             size="sm"
             centered
             no-close-on-backdrop>
      <template slot="modal-header">
        <div style="width: 100%">
          <b-row>
            <!-- <b-col><span v-html="modalTitle()"></span></b-col> -->
            <b-col><span>New Stream</span></b-col>
          </b-row>
        </div>
      </template>
      <div>
        <!-- error placeholder -->
        <b-alert v-if="error" show variant="danger">{{error.message}}</b-alert>

        <!-- form -->
        <div class="field-container">
          <div class="label">stream name</div>
          <input v-model="channel.name"
                 class="input"
                 placeholder="#stream_name"
                 @keypress="onInputChange('name')" />
          <p v-show="formErrors.name"
             class="text-danger">specify stream name</p>
        </div>
        <div class="field-container">
          <div class="label">hosting region</div>
          <b-form-select v-model="channel.region"
                         class="input"
                         @change="onInputChange('region')">
            <!-- <option v-for="region in getHostingRegions()" -->
            <option v-for="region in regions"
                    :key="region._id"
                    :value="region._id">{{region.name}}</option>
          </b-form-select>

          <p v-show="formErrors.region"
             class="text-danger">specify hosting region</p>
        </div>
        <!-- <br> -->
      </div>

      <template slot="modal-footer" class="text-left">
        <!-- <br> -->
        <b-progress v-if="processing" 
                    :value="100" 
                    :max="100" 
                    animated
                    class="w-75"></b-progress>
        <div v-else>
          <button type="button" 
                  class="modal-button" 
                  @click="dismiss">Cancel</button>
          <button @click="onSaveChannel"
                  type="submit"
                  class="modal-button highlight">Save</button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import _ from "lodash";
import * as api from "../api";
import HostingRegions from "./hostingRegions";
import StreamService from '../services/StreamService'

export default {
  name: "AddChannelModal",
  async mounted () {
    this.$refs.modalAddChannel.$on('shown', this.onInit)    
    this.regions = await StreamService.getAvailableRegions()
  },
  data() {
    return {
      processing: false,
      error: null,
      channel: {
        name: null,
        region: null
      },
      regions: [],
      formErrors: { name: false, region: false },
      onInputChange(prop) {
        this.formErrors[prop] = false;
      }
    };
  },
  methods: {
    onInit () {},
    getHostingRegions() {
      return HostingRegions
    },
    async onSaveChannel() {
      this.error = null
      
      if (!this.validateForm()) return;
      this.processing = true;
      
      try {
        const stream = await StreamService.addStream(this.channel.name, this.channel.region)
        this.$emit("new-channel", stream);
        this.dismiss();
      
      } catch (err) {
        this.error = err
        this.processing = false
      }

    },
    dismiss() {
      this.$refs.modalAddChannel.hide();
      // clear up cache
      this.resetForm()
      this.clearErrors()
      
      // lazy clear
      setTimeout(() => { this.processing = false; }, 1000);
    },
    validateForm() {
      const props = ["name", "region"];

      let valids = 0;
      _.each(props, prop => {
        const val = this.channel[prop];
        if (val) valids++;
        // else this.formErrors[prop] = true;

        this.formErrors[prop] = !val;
      });

      return valids === props.length;
    },
    clearErrors() {
      this.formErrors.name = false;
      this.formErrors.region = false;
    },
    resetForm () {
      this.channel.name = null
      this.channel.region = null
    }
  },
  components: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.modal-button {
  border: none;
  padding: 7px 30px;
  font-size: 15.5px;
  color: #f7f7f7;
  background-color: transparent;
  text-transform: capitalize;
  font-size: 13.5px;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  border-radius: 50px;
  outline: none;
}
.modal-button:hover {
  background-color: rgb(13, 15, 61);
}
.modal-button:active {
  background-color: rgb(13, 15, 50);
}
.modal-button.highlight {
  background-color: #ab3147;
}
.modal-button.highlight:hover {
  background-color: #8d1d32;
}
.field-container {
  width: 100%;
  padding: 10px 0;
}
.field-container:last-of-type {
  border-bottom: none;
}
.label {
  font-size: 12px;
  opacity: 0.65;
  text-transform: capitalize;
}
.input {
  display: block;
  width: 100%;
  height: auto !important;
  margin: 7px 0 7px 0;
  padding: 10px 14px;
  color: #ffffff;
  background-color: #010329;
  border: none;
  border-radius: 2px;
}
.input:focus {
  background-color: rgba(1, 3, 41, 0.47);
}
</style>



