<template>
  <div class="card"
     v-bind:class="{ restream: true, 'opacity-75 no-pointer': stream.removing }">
    <div class="thumb">
      <stream-thumb :stream="stream" class="video-thumb" />
    </div>
    <div class="row">
      <div class="col-md-12">
        <div class="caption">
          <div class="title">
            <div layout>
              <!-- <div flex="nogrow">
                <span ng-show="stream.pulse"
                      ng-class="vm.getStatusClass()"
                      class="status"></span>
                <div ng-if="!stream.pulse"
                     class="status">
                  <md-progress-circular md-mode="indeterminate"
                                        md-diameter="13"></md-progress-circular>
                </div>
              </div> -->
              <div flex
                   class="set-padding-l-xs">
                <div class="word-wrap">{{stream.name}}</div>
              </div>
            </div>
          </div>

          <div class="description">
            <div>
              <img class="media-flag"  :src="getCountryFlag(stream)" />
              <div class="media-region">{{stream.region.name}}</div>
            </div>

            <div v-if="stream.pulse"
                 class="media-info">
              <div>
                <!-- <span ng-hide="stream.pulse.bitrate || stream.pulse.trackstream.length"> -->
                <div v-if="stream.pulse.alive">
                  <span v-for="track in stream.pulse.tracks"
                        v-bind:key="track.id"
                        v-bind:class="getTrackType(track)"
                        class="media-icon"
                        title="stream codec"><span>{{track.codec}}</span>
                  </span>
                  <span v-show="stream.pulse.bitrate"
                        class="font-num font-sm">
                  <span>{{stream.pulse.bitrate}}</span>kbps
                  </span>
                </div>
                <div v-else><code>waiting for stream</code></div>
              </div>

              <!-- <div class="media-slot">
                <div v-if="hasTag('ondemand')"
                     class="media-slot-item">
                  <code class="font-sm">ondemand</code>
                </div>
                <div v-if="hasFeature('dvr')"
                     class="media-slot-item"
                     title="DVR is enabled">
                  <span class="dvr no-b">
                    <span class="text-red-dark fa fa-circle-o"></span>&nbsp;<span>DVR</span>
                  </span>
                </div>
                <div ng-if="stream.pulse.delay > 1000"
                     class="media-slot-item"
                     title="DVR is enabled">
                  <span class="set-padding-t-xs text-muted no-b"
                        title="frames delay">
                    <span class="fa fa-pause"></span>&nbsp;<span ng-bind="stream.pulse.delay | shortTime:true"></span>
                  </span>
                </div>
              </div> -->
            </div>
            <!-- <div v-else
                 class="set-margin-b-sm">stats n/a </div> -->
            <div v-else></div>
          </div>
          <!-- <div class="date font-xs"
               v-bind:title="lastEditTitle()">last edit
            <span>{{stream.last_edit}}</span>
          </div> -->
        </div>
      </div>
    </div>    
    <!-- <dropdown menu="stream.actionMenu"></dropdown> -->
    <div class="row card-controls">
      <div class="col-md-6">
        <b-dropdown id="card-dd" class="m-md-2" size="sm" variant="link" no-caret>
          <template slot="button-content">
            <!-- <i class="fas fa-ellipsis-h" style="color: white;"></i> -->
            <i class="fa fa-cog dd-icon"></i>
          </template>
          <b-dropdown-item>
            <router-link :to="{ name: 'ChannelManage', params: { streamId: stream._id } }">Manage</router-link>
          </b-dropdown-item>
          <!-- <b-dropdown-item disabled>
            <a href="#">Configure</a>
          </b-dropdown-item> -->
          <b-dropdown-divider></b-dropdown-divider>
          <b-dropdown-item @click="$emit('delete-stream', stream)">
            <a href="#">Delete</a>
          </b-dropdown-item>
        </b-dropdown>
      </div>
      <div class="col-md-6 p-0">
              <!-- :class="{ 'fa-toggle-on enabled': streamStatus,  'fa-toggle-off': !streamStatus 'status-processing': }" -->
        <span class="fas toggle-control"
              v-bind:class="{ 'fa-toggle-on enabled': streamStatus, 
                              'fa-toggle-off': !streamStatus,
                              'status-processing': statusProcessing }"
              @click="toggleStatus"></span>
      </div>
    </div>
  </div>
</template>

<script>
import StreamThumb from './StreamThumb.vue'
import StreamService from '../services/StreamService'

export default {
  name: "StreamCardView",
  props: ["stream"],
  mounted () {
  // const {haxrBlockId} = this.stream
  // this.$socket.emit('stream.summary', haxrBlockId, { out: 'media', subscribe: true })
  // this.$socket.$on('summary.' + haxrBlockId, (res) => {
  //   console.log('res', res)
  // })
  },
  data() {
    return {
      streamStatus: this.stream.enabled,
      statusProcessing: false
    };
  },
  methods: {
    getCountryFlag(stream) {
      return `http://www.countryflags.io/${
        stream.region.identifier
      }/flat/24.png`;
    },
    async toggleStatus() {
      const oldStatus = this.streamStatus
      const newStatus = !oldStatus

      this.statusProcessing = true

      try {
        await StreamService.toggleStream(this.stream._id)
        this.streamStatus = newStatus

      } catch (err) {
        streamStatus = oldStatus
        this.$notify({ group: "error", title: "Couldn't toggle stream status", text: err.message });
      }
      
      this.statusProcessing = false
    },
    getTrackType(track) {
      if (!track) return;

      var type;
      var id = track.id;
      if (/^a/gi.test(id)) type = "audio";
      if (/^v/gi.test(id)) type = "video";

      return type;
    },
    lastEditTitle() {
      return "stream was last edited on " + this.stream.last_edit;
    }
  },
  components: { StreamThumb }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.card {
  margin: 0;
  padding: 0;
  color: #f7f7f7;
  background-color: rgb(23, 25, 72);
  overflow: visible;
  width: 100%;
  min-height: 360px;
  box-sizing: border-box;
  border-radius: 2px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14),
    0 2px 1px -1px rgba(0, 0, 0, 0.12);
  /* border: 1px solid rgba(255, 255, 255, 0.1); */
}

.card:hover {
  border: 1px solid rgba(255, 255, 255, 0.2);
  /* box-shadow: 0 0 6px rgba(0, 0, 0, .3); */
}

.card .thumb {
  /* background-image: url(../assets/placeholder.jpg); */
  background-size: auto 130%;
  background-position: center center;
  background-repeat: no-repeat;
  height: 225px;
  /* background-color: #17193e; */
  background-color: #000000;
  position: relative;
}

.card .thumb .paused-label {
  position: absolute;
  bottom: 10px;
  width: auto;
  right: 10px;
  font-size: 13px;
  text-transform: uppercase;
  color: #ffffff;
  display: inline-block;
  font-weight: 500;
  padding: 2px 6px;
  background-color: rgb(51, 103, 214);
  border-radius: 2px;
}

.card .caption {
  /* font-family: "Poppins"; */
  padding: 12px 5px 12px 12px;
  border-top: 1px solid rgba(0, 0, 0, 0.07);
  font-weight: 400;
  float: left;
  width: 80%;
}

.card .action {
  float: right;
  padding: 10px;
  text-align: center;
  border: none;
  background: none;
  font-size: 2em;
  line-height: 1;
  display: block;
  outline: none;
}

.card .caption .title {
  font-size: 24px;
  font-weight: 700;
  color: #f7f7f7;
  letter-spacing: -0.5px;
}

.card .caption .title .status {
  font-size: 10px;
  vertical-align: middle;
  margin-right: 3px;
  opacity: 0.75;
}

.card .caption .title .status md-progress-circular {
  vertical-align: middle;
  margin-left: -3px;
  display: inline-block;
}

.card .caption .title .status.pending {
  opacity: 1;
}
.card .description {
  margin-top: 5px;
}
.card .media-region {
  color: white;
  /* padding: 1px 7px; */
  /* background: #09af90; */
  display: inline-block;
  /* border-radius: 2px; */
  font-size: 14px;
  letter-spacing: -0.5px;
}
.card .media-flag {
  display: inline-block;
  max-width: 20px;
  margin-right: 3px;
}

#card-dd {
  margin: 0 !important;
  margin-top: 10px !important;
  margin-left: 5px !important;
}
.card-icon {
  margin: 0 !important;
  margin-top: 10px !important;
  margin-left: 5px !important;
}
.dd-icon {
  color: #ffffff;
  font-size: 16px;
}
.dd-icon:hover {
  opacity: 0.5;
}
.card-controls {
  width: 100%;
  bottom: 10px;
  position: absolute;
}
.toggle-control {
  float: right;
  color: #ffffff;
  font-size: 18px;
  margin: 15px 0 0 5px;
  transition: all 0.2s linear;
}
.toggle-control.enabled {
  /* color: #3d9bff; */
  color: greenyellow;
}
.toggle-control.status-processing {
  cursor: not-allowed;
  opacity: 0.65;
  color: #ffffff;
}
</style>



