<template>
  <div class="view">
    <div class="title">Billing</div>
    <br>
    <div class="row-container">
      <b-row>
        <b-col>
          <div class="para-title">Payment Method</div>
        </b-col>
        <b-col class="text-right text-dimm">
          <span v-if="paymentMethod">
             <strong class="text-uppercase">visa</strong> ending in
          </span>
          <span v-else>none specified</span>
          <b-button variant="link" size="sm">Update</b-button>
        </b-col>
      </b-row>
    </div>
    <div class="row-container">
      <b-row>
        <b-col>
          <div class="para-title">Subscription</div>
        </b-col>
        <b-col class="text-right text-dimm">
          <span class="subscription-badge">Freemium</span>&nbsp;
          <!-- <code style="font-size:12.5px;">USD $19.99/month</code>&nbsp;
          <span>
             valid through <strong>9th June</strong>
          </span>
          <b-button variant="link" size="sm">Update</b-button> -->
        </b-col>
      </b-row>
    </div>
    <br>
    <div class="row-container"
         style="border:none;">
      <div class="para-title">Transactions</div>
      <div class="text-dimm">Below are the billing transactions debited through Castr</div>
    </div>
    <br>
    <br>
    <b-table :fields="transactionsTableFields" 
             :items="paymentHistory"
             :hover="true"
             class="">
      <template slot="time" slot-scope="data">
        {{data.value | date('DD/MM/YYYY')}}
      </template>
      <template slot="cycle" slot-scope="data">
        {{data.value.start | date('DD/MM/YYYY')}} <span class="text-dumm">through</span> {{data.value.end | date('DD/MM/YYYY')}}
      </template>
      <template slot="bill" slot-scope="data">
        USD ${{data.value.net}}
      </template>
      <template slot="actions" slot-scope="data">
        <b-button variant="link" 
                  size="sm">Invoice</b-button>
      </template>
    </b-table>
    <p v-if="!paymentHistory.length" 
       class="text-center">no transactions to display</p>

  </div>
</template>

<script>
export default {
  name: "ManagePasswordChange",
  data() {
    return {
      paymentMethod: null,
      paymentHistory: [
        // { 
        //   id: 'm87jdhcw0e8r7', 
        //   cycle: { start: new Date(Date.now()-(30*24*3600000)), end: new Date() },
        //   bill: { net: 19.99 },
        //   time: new Date()
        // },
        // { 
        //   id: 'cimosyndnfy', 
        //   cycle: { start: new Date(Date.now()-(2*30*24*3600000)), end: new Date(Date.now()-(30*24*3600000)) },
        //   bill: { net: 19.99 },
        //   time: new Date(Date.now()-(30*24*3600000))
        // }
      ],
      transactionsTableFields: [
        { key: 'time', label: 'Date'},
        'cycle',
        { key: 'bill', label: 'Billed' },
        { key: 'actions', label: ' ' },
      ],
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.view {
  color: #f7f7f7;
}
.title {
  color: inherit;
  font-size: 20px;
}
.row-container {
  padding: 15px 5px;
  border-bottom: 1px solid #26284f;
}
.para-title {
  font-size: 15.5px;
}

.field-container {
  width: 235px;
  padding: 5px 0;
}
.field-container:first-of-type {
  padding-top: 32px;
}
.label {
  font-size: 12px;
  opacity: 0.65;
  text-transform: capitalize;
}
.input {
  display: block;
  width: 100%;
  margin: 7px 0 14px 0;
  padding: 10px 14px;
  color: #ffffff;
  background-color: #010329;
  border: none;
  border-radius: 2px;
}
.input:focus {
  background-color: rgba(1, 3, 41, 0.47);
}
.button {
  font-size: 12px;
  padding-right: 12px;
  padding-left: 12px;
}
.subscription-badge {
  border-radius: 2px;
  padding: 4px 7px;
  font-size: 13px;
  letter-spacing: -0.5px;
  background: #dc3545;
  color: #ffffff;
  display: inline-block;
}
</style>



