<template>
  <div class="">
    <!-- Modal Component -->
    <b-modal ref="modalConfigurePlatform" 
             id="modal-configure-platform"
             class="modal-platform"
             size="sm"
             centered
             no-close-on-backdrop>
      <template slot="modal-header">
        <div style="width: 100%">
          <b-row>
            <b-col><span v-html="modalTitle()"></span></b-col>
          </b-row>
        </div>
      </template>
      <div>
        <!-- error placeholder -->
          <b-alert v-if="error"
                   show
                   variant="danger">{{error.message}}</b-alert>
        <!-- form -->
        <div class="field-container">
          <div class="label">streaming {{ platform.custom && 'platform address' || 'server' }} </div>

          <input v-if="platform.custom"
                 v-model="platformConfig.server"
                 class="input"
                 placeholder="rtmp://braodcaster_addr/"
                 @keypress="onInputChange('server')" />
          <b-form-select v-else
                         v-model="platformConfig.server"
                         :options="getPlatformServers(platform.name)"
                         class="input"
                         @change="onInputChange('server')"></b-form-select>

          <p v-show="formErrors.server"
             class="text-danger">
            specify {{ platform.custom && 'destination' || platform.name }} server
          </p>
        </div>
        <div class="field-container">
          <div class="label">streaming key</div>
          <input v-model="platformConfig.key"
                 class="input"
                 placeholder="#streaming_key"
                 @keypress="onInputChange('key')" />
          <p v-show="formErrors.key"
             class="text-danger">specify stream key</p>
        </div>
        <br>
        <br>
      </div>

      <template slot="modal-footer" class="text-left">
        <br>
        <b-progress v-if="processing" 
                    :value="100" 
                    :max="100" 
                    animated
                    class="w-75"></b-progress>
        <div v-else>
          <button type="button" 
                  class="modal-button" 
                  @click="dismiss">Cancel</button>
          <button @click="onSavePlatform"
                  type="submit"
                  class="modal-button highlight">Save</button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import _ from "lodash";
import StreamService from "../../services/StreamService";
import PlatformConfigurations from "./platformConfigurations";

const ModalTitles = ["Specify Streaming Platform", ""];
const Platforms = [
  { name: "youtube", icon: "youtube" },
  { name: "twitch", icon: "twitch" }
];

export default {
  name: "AddPlatformModal",
  props: ["stream", "platform"],
  mounted () {
    this.$refs.modalConfigurePlatform.$on('shown', this.onInit)
  },
  data() {
    return {
      error: null,
      processing: false,
      platformTemplates: Platforms,
      platformConfig: { server: null, key: null },
      formErrors: { server: false, key: false },
      modalTitle() {
        let title;
        if (this.platform.custom) title = this.platform.name;
        else
          title =
            `<i class="fab fa-${this.platform.name}"></i> &nbsp;` +
            _.capitalize(this.platform.name);

        return title;
      },
      onInputChange(prop) {
        this.formErrors[prop] = false;
      }
    };
  },
  methods: {
    onInit () {
      this.platform.custom = this.platform.template == 'custom'
      
      if (!this.platformConfig.server)
        this.platformConfig.server = this.platform.server
    
      if (!this.platformConfig.key)
        this.platformConfig.key = this.platform.key
    },
    getPlatformServers(platformId) {
      const config = PlatformConfigurations[platformId] || {};
      return config.servers || [];
    },
    async onSavePlatform() {
      this.error = null
      if (!this.validateForm()) return;

      const {server,key} = this.platformConfig

      if (this.platform.server === server && 
         this.platform.key === key) {
        this.dismiss()
        return
      }

      this.processing = true;

      try {
        await StreamService.setStreamPlatformAddress(this.stream._id, this.platform._id, { server, key })
        this.$emit("platform-updated", this.platform, this.platformConfig);
        this.dismiss();
      } catch (err) {
        this.error = err
        this.processing = false
      }

    },
    dismiss() {
      this.$refs.modalConfigurePlatform.hide();
      // clear up cache
      this.resetForm()
      this.clearErrors();

      
      // lazy clear
      setTimeout(() => { this.processing = false; }, 1000);
    },
    validateForm() {
      const props = ["server", "key"];

      let valids = 0;
      _.each(props, prop => {
        const val = this.platform[prop];
        if (val) valids++;
        // else this.formErrors[prop] = true;

        this.formErrors[prop] = !val;
      });

      return valids === props.length;
    },
    clearErrors() {
      this.formErrors.server = false;
      this.formErrors.key = false;
    },
    resetForm () {
      this.platformConfig.server = null
      this.platformConfig.key = null
    }
  },
  components: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.platforms-template-list {
  padding-bottom: 30px;
}

.platform-item {
  display: inline-block;
  padding: 7px;
  width: 120px;
  height: 110px;
  box-sizing: border-box;
  /* border-right: 1px solid rgb(13, 15, 61); */
  text-align: center;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  border-radius: 3px;
}
.platform-item:hover {
  /* background-color: rgb(20, 21, 59); */
  background-color: rgb(13, 15, 61);
  border-color: rgb(20, 21, 59);
}
.platform-item:last-of-type {
  border-color: transparent;
}
.platform-icon {
  font-size: 42px;
  height: 65px;
  padding-top: 7px;
}
.platform-label {
  opacity: 0.75;
  font-size: 12px;
  text-transform: capitalize;
  font-weight: 600;
}
.modal-button {
  border: none;
  padding: 7px 30px;
  font-size: 15.5px;
  color: #f7f7f7;
  background-color: transparent;
  text-transform: capitalize;
  font-size: 13.5px;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  border-radius: 50px;
  outline: none;
}
.modal-button:hover {
  background-color: rgb(13, 15, 61);
}
.modal-button:active {
  background-color: rgb(13, 15, 50);
}
.modal-button.highlight {
  background-color: #ab3147;
}
.modal-button.highlight:hover {
  background-color: #8d1d32;
}
.field-container {
  /* width: 235px; */
  width: 100%;
  padding: 10px 0;
}
.field-container:last-of-type {
  border-bottom: none;
}
.label {
  font-size: 12px;
  opacity: 0.65;
  text-transform: capitalize;
}
.input {
  display: block;
  width: 100%;
  height: auto !important;
  margin: 7px 0 7px 0;
  padding: 10px 14px;
  color: #ffffff;
  background-color: #010329;
  border: none;
  border-radius: 2px;
}
.input:focus {
  background-color: rgba(1, 3, 41, 0.47);
}
</style>



